.s-card{background-color:var(--theme-cards);color:var(--theme-text-primary);display:block;max-width:100%;outline:none;text-decoration:none;transition-property:box-shadow, opacity;overflow-wrap:break-word;position:relative;white-space:normal}.s-card:not(.flat){box-shadow:0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12)}.s-card:not(.tile){border-radius:4px}.s-card.outlined{border:thin solid rgba(0, 0, 0, 0.12);box-shadow:0 0 0 0 rgba(0, 0, 0, 0.2), 0 0 0 0 rgba(0, 0, 0, 0.14), 0 0 0 0 rgba(0, 0, 0, 0.12)}.s-card.raised{box-shadow:0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12)}.s-card.shaped{border-radius:24px 4px}.s-card.hover{cursor:pointer;transition:box-shadow 0.4s cubic-bezier(0.25, 0.8, 0.25, 1)}.s-card.hover:hover,.s-card.hover:focus{box-shadow:0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12)}.s-card.link{cursor:pointer}.s-card.link .s-chip{cursor:pointer}.s-card.link::before{background:currentColor;bottom:0;content:"";left:0;opacity:0;pointer-events:none;position:absolute;right:0;top:0;transition:0.2s opacity}.s-card.link:focus::before{opacity:0.08}.s-card.disabled{pointer-events:none;user-select:none}.s-card.disabled>*:not(.s-progress-linear){opacity:0.6;transition:inherit}.s-card>*:first-child:not(.s-btn):not(.s-chip),.s-card>[slot=progress]+*:not(.s-btn):not(.s-chip){border-top-left-radius:inherit;border-top-right-radius:inherit}.s-card>*:last-child:not(.s-btn):not(.s-chip){border-bottom-left-radius:inherit;border-bottom-right-radius:inherit}
