.s-checkbox{display:flex;align-items:center;position:relative}.s-checkbox label{padding-left:12px}.s-checkbox__background{width:100%;height:100%;align-items:center;display:inline-flex;border:2px solid currentColor;border-radius:2px;color:inherit;background-color:transparent;transition:background-color 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);pointer-events:none}.s-checkbox__wrapper{overflow:unset !important;display:inline-flex;justify-content:center;align-items:center;flex:0 0 auto;height:18px;width:18px;position:relative;user-select:none;border-radius:100%}.s-checkbox__wrapper::before{border-radius:inherit;content:"";top:0;bottom:0;left:0;right:0;position:absolute;width:100%;height:100%;background-color:currentColor;transform:scale(2);opacity:0}.s-checkbox__wrapper:not(.disabled){color:var(--theme-text-secondary);cursor:pointer}.s-checkbox__wrapper:not(.disabled):hover::before{opacity:0.16}.s-checkbox__wrapper.disabled{opacity:0.6;color:var(--theme-controls-disabled) !important}.s-checkbox__wrapper input{position:absolute;width:36px;height:36px;cursor:inherit;opacity:0;margin:0;padding:0}.s-checkbox__wrapper input:checked~.s-checkbox__background,.s-checkbox__wrapper input:indeterminate~.s-checkbox__background{background-color:currentColor;border:none}.s-checkbox__wrapper svg{position:absolute;top:0;left:0;right:0;bottom:0;width:100%;height:100%;color:#ffffff;padding:1px}.s-checkbox__wrapper svg path{stroke:currentColor}
