.s-dialog{align-items:center;display:flex;height:100%;justify-content:center;left:0;pointer-events:none;position:fixed;top:0;transition:0.2s cubic-bezier(0.25, 0.8, 0.25, 1), z-index 1ms;width:100%;z-index:6;outline:none}.s-dialog__content{background-color:var(--theme-surface);border-radius:4px;margin:24px;overflow-y:auto;pointer-events:auto;z-index:inherit;box-shadow:0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12)}.s-dialog__content:not(.fullscreen){max-height:90%;width:var(--s-dialog-width)}.s-dialog__content.fullscreen{border-radius:0;margin:0;height:100%;width:100%;position:fixed;overflow-y:auto;top:0;left:0}.s-dialog__content>.s-card>.s-card-title{font-size:1.25rem;font-weight:500;letter-spacing:0.0125em;padding:16px 24px 10px}.s-dialog__content>.s-card>.s-card-text{padding:0 24px 20px}.s-dialog__content>.s-card>.s-card-subtitle{padding:0 24px 20px}.fullscreen{border-radius:0;margin:0;height:100%;position:fixed;overflow-y:auto;top:0;left:0}.fullscreen>.s-card{min-height:100%;min-width:100%;margin:0 !important;padding:0 !important}
